.auth-section {
  padding: 120px 0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth-container {
  max-width: 400px;
  width: 100%;
  background: var(--card-bg);
  padding: 40px;
  border-radius: 15px;
  border: 1px solid var(--border-color);
}

